import React from 'react';

const Start = (props) => {
  const handleStart=props.handleStart;
  return(
    <div >
        <h3>Cmo jugar</h3>
        <p>Interven en situaciones cotidianas y pon en prctica lo que sabs y lo que aprendiste en la Caja de Herramientas.</p>
        <p>Interactu con los textos para encontrar expresiones excluyentes y reemplazalas con una opcin ms inclusiva.</p>
        <button onClick={handleStart}>Comenzar</button>
    </div>
  );
}

export default Start;